<template>
   <div class="form-container">
      <div class="newForm-panel" v-for="(value,key) in childData.data">
         <div class="leftItems">{{key}}<i class="required-lable" v-if="checkReq(value)">*</i></div>
         <div class="rightItems" >
            <div v-if="judgeType(value)">
               <Input v-model="childData.formItem[childData.Id+childData.key+key]"
                      v-bind:placeholder="'please enter '+key+' value'"
                      class="form-input" @on-blur="validate(value,key,childData.Id+childData.key+key)"></Input>
               <Button type="primary" size="small" v-if="cumputerLongTime(value)" style="margin-left: 8px;"
                       v-on:click="setTimestamp(childData.Id+childData.key+key)">timestamp
               </Button>
            </div>
            <div v-else style="width: 100%">
               <div class="newForm-panel" v-for="(v,k) in value">
                  <div class="leftItems">{{k}}<i class="required-lable" v-if="checkReq(value)">*</i></div>
                  <div class="rightItems">
                     <Input v-model="childData.formItem[childData.Id+childData.key + key +k]"
                            v-bind:placeholder="'please enter '+k + ' value'" class="form-input"
                            @on-blur="validate(v,k,childData.Id+childData.key + key +k)"></Input>
                     <Button type="primary" size="small" v-if="cumputerLongTime(v)" style="margin-left: 8px;"
                             v-on:click="setTimestamp(childData.Id+childData.key + key +k)">timestamp
                     </Button>
                  </div>
               </div>
            </div>
         </div>
      </div>
   </div>
</template>

<script>
   export default {
      props: ["childData"],
      name: "form_panel",
      data() {
         return {
            docName: "",
            nextData: {},
            dataKey: ''
         }
      },
      created(){
      },
      methods: {
         cumputerLongTime(valueStr) {
            var value = valueStr.split(",");
            if (value[0] === "int64") {
               return true;
            }
            return false;
         },
         setTimestamp(key) {
            this.childData.formItem[key] = new Date().getTime();
         },
         getInputType(fieldType) {
            if (fieldType === "string") {
               return false;
            }
            if (fieldType === "int32" || fieldType === "float64" || fieldType === "int64") {
               return true;
            }
         },
         judgeType(value) {
            return typeof value !== "object";
         },
         validate(valueStr, key, vmodelKey) {
            var value = valueStr.split(",");
            var v = this.childData.formItem[vmodelKey];
            if (v == undefined){
               return
            }
            if (v == ""){
               return
            }
            if (v.trim() === "") {
               return;
            }
            if (this.getInputType(value[0]) && !this.isNum(v)) {
               this.childData.formItem[vmodelKey] = "";
               this.$Notice.error({
                  title: '',
                  desc: key + ''
               });
            }
         },
         checkReq(fieldInfo) {
            if (typeof fieldInfo == "object"){
               return true;
            }
            var fno = fieldInfo.split(",");
            if (fno[1] == "req") {
               return true;
            }
            return false;
         },
         isNotNull(str) {
            if (str === undefined || str === null) {
               return false;
            }
            if (str.trim() === "") {
               return false;
            }
            return true;
         },
         isNum(val) {
            if (val === "" || val === null) {
               return false;
            }
            if (!isNaN(val)) {
               return true;
            } else {
               return false;
            }
         }
      }
   }
</script>

<style>
   .newForm-panel {
      width: 100%;
      -webkit-flex-wrap: wrap;
      flex-wrap: wrap;
      display: -webkit-flex;
      display: flex;
      margin-bottom: 10px;
   }

   .newForm-panel .leftItems {
      width: 15%;
      text-align: left;
      position: relative;
   }

   .newForm-panel .rightItems {
      width: 85%;
      -webkit-flex-wrap: wrap;
      flex-wrap: wrap;
      display: -webkit-flex;
      display: flex;
   }
   .form-container{
      width: 100%;
   }
</style>


