import ShaUtil from './sha_dev';


var Context = {};

Context.generateSHA = function (src) {
   var stringBuilder = "";
   if (src === null || src.length <= 0) {
      return null;
   }
   for (var i = 0; i < src.length; i++) {
      var indexValue = src[i];
      var v = indexValue & 0xFF;
      var hv = v.toString(16);
      if (hv.length < 2) {
         stringBuilder += "0";
      }
      stringBuilder += hv;
   }
   return stringBuilder;
};
Context.byte = function (str) {
   var ch = [];
   var st = [];
   var re = [];
   for (var i = 0; i < str.length; i++) {
      ch = str.charCodeAt(i);
      st = [];
      do {
         st.push(ch & 0xFF);
         ch = ch >> 8;
      }
      while (ch);
      re = re.concat(st.reverse());
   }
   return Uint8Array.from(re);
};
Context.byteMerger = function (byte1, byte2) {
   var len = byte1.length + byte2.length;

   var byte3 = new Array(len);

   for (var i = 0; i < byte1.length; i++) {
      byte3[i] = byte1[i];
   }
   for (var index = 0; index < byte2.length; index++) {
      byte3[index + byte1.length] = byte2[index];
   }
   return Uint8Array.from(byte3);
};
Context.getRequestHeader = function (buffer, key) {

   var timeStamp = new Date().getTime().toString();
   var buf = Context.byteMerger(Context.byte(key), Context.byte(timeStamp));
   var bodyBuffer = Context.byteMerger(buf, buffer);

   var hasher1 = new ShaUtil('SHA-1', 'ARRAYBUFFER');
   hasher1.update(bodyBuffer);
   var arrayBuffer = hasher1.getHash('ARRAYBUFFER');

   var header = {
      sign: Context.generateSHA(new Int8Array(arrayBuffer)),
      timeStamp: timeStamp
   };

   return header;
};

export default Context;



